<template>
  <div id="app">
    <router-view/>
    <transition name="fade">
      <div class="tips" v-if="tips.toggle">{{tips.desc}}</div>
    </transition>
    <transition name="fade">
      <div class="buy" v-if="buy.toggle">
        <div class="buy-template">
          <div class="close" @click="buy.toggle = false">×</div>
          <img class="img" src="./assets/prize_box_no_bg.png"/>
          <p class="sentance">{{buy.desc}}</p>
          <div class="button">{{buy.button_desc}}</div>
        </div>
      </div>
    </transition>
    <div class="recevier-data" v-if="recevier_data.toggle">
      <div class="template">
        <div class="close" @click="recevier_data.toggle = false">×</div>
        <div class="data" v-if="recevier_data.type === 1" v-html="recevier_data.data"></div>
        <div class="data" v-else>
          <div class="input">
            <div class="item"><p>收件人:</p><input v-model="user_input.user_name"/></div>
            <div class="item"><p>手机号:</p><input v-model="user_input.user_number"/></div>
            <div class="item"><p>地址:</p><textarea v-model="user_input.user_address" class="fuck" ></textarea></div>
            <div class="my-button" @click="submit">确定</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
// import Share from '@/js/shareAndGetName';
import BScroll from 'better-scroll'
import { callAppRouter } from '@/js/tool'
export default {
  data () {
    return {
      tips: {
        desc: '',
        toggle: false,
        busy: false
      },
      buy: {
        toggle: false,
        desc: '暂无抽奖资格',
        button_desc: '开通会员获抽奖资格'
      },
      url: 'https://api.debug.psy-1.com',
      openid: '123',
      recevier_data: {
        data: '',
        toggle: false,
        type: 1
      },
      click_once: [{ one: false }],
      user_input: {
        user_name: '',
        user_number: '',
        user_address: ''
      },
      ticket_code: ''
    }
  },
  methods: {
    bScroll () {
      const wrapper = document.querySelector('#app')
      this.better_scroll = new BScroll(wrapper, {
        click: true,
        bounce: {
          top: false,
          bottom: false,
          left: false,
          right: false
        }
      })
    },
    toMemberCenter () {
      callAppRouter('Redirect', {
        code: ''
      })
    },
    Tips (message) {
      this.tips.desc = message
      if (!this.tips.busy) {
        this.tips.toggle = true
        this.tips.busy = true
        setTimeout(() => {
          this.tips.toggle = false
          this.tips.busy = false
        }, 2000)
      }
    },
    userData (data, type = 2, code, ticketCode) {
      this.ticket_code = ticketCode
      this.recevier_data.toggle = true
      this.recevier_data.data = data
      this.recevier_data.type = type
    },
    submit () {
      if (this.click_once[0].one) {
        return
      }
      this.click_once[0].one = true
      setTimeout(() => {
        this.click_once[0].one = false
      }, 300)
      this.recevier_data.toggle = false
      const receiver = {
        user_name: this.user_input.user_name,
        user_number: this.user_input.user_number,
        user_address: this.user_input.user_address
      }
      // eslint-disable-next-line
      $.post(this.url + '/web/v1/vip/lottery/complete', {openid: this.openid, ticket_id: this.ticket_code, ticket_receiver: JSON.stringify(receiver)}, (data) => {
        console.log(data.msg)
        if (data.msg === '成功') {
          this.Tips('成功')
          this.$children[0].getRewardData()
        }
      })
    }
  },
  mounted () {
    // console.log(WJH)
    // this.test()
    // document.body.addEventListener('touchstart', function () {})
  }
}
</script>

