<template>
  <div class="reward">
    <div class="reward-template">
      <div class="reward-template-outermg">
        <img class="reward-template-outermg-titlemg" src="../assets/myprize.png" />
      </div>
      <transition-group name="fade">
        <div v-for="(li, index) in list" :key="index" class="list">
          <p v-html="li.desc"></p>
          <span @click="detail(li.get, li.vitual, li.ticket_receiver, li.ticket_code, li.ticket_id)">{{li.get ? '详情' : '领取'}}</span>
        </div>
      </transition-group>
      <div v-if="list_null" style="padding-top: 10%;color: #fff;">
        暂无中奖物品
      </div>

      <div class="reward-template-outer" v-if="nothing">
      <img class="reward-template-outer-bg" src="../assets/prize_no2.png" />
      <p class="reward-template-outer-desc">暂无中奖奖品</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Reward',
  data () {
    return {
      nothing: false,
      list: [],
      list_null: false,
      click_once: [{ one: false }]
    }
  },
  methods: {
    getRewardData () {
      this.list = []
      // eslint-disable-next-line
      $.get(this.$parent.url + '/web/v1/vip/lottery/history?openid=' + this.$parent.openid, (data) => {
        for (let i of data.data) {
          let desc = ''
          let get = true
          let vitual = false
          let func = () => {
            i.ticket_receiver ? desc += ',已领取' : desc += `,<span style="color: red; opacity: 0.7">&nbsp;未领取`
            i.ticket_receiver ? get = true : get = false
          }
          const d = parseInt(i.ticket_code)
          switch (true) {
            case d === 0: desc = '1999元 Bose 遮噪睡眠耳塞'; func(); break
            case d === 1: desc = '30元小睡眠1个月会员,已领取'; vitual = true; break
            case d === 2: desc = '49元 热敷眼罩'; func(); break
            case d === 4: desc = '499元 智能音箱'; func(); break
            case d === 5: desc = '268元小睡眠12个月会员,已领取'; vitual = true; break
            case d === 6: desc = '300元 MUJI 小型超声波香薰机'; func(); break
            case d === 7: desc = '小睡眠7天会员,已领取'; vitual = true; break
          }
          const content = {
            desc,
            get,
            ticket_code: i.ticket_code,
            vitual,
            ticket_receiver: i.ticket_receiver,
            ticket_id: i.ticket_id
          }
          this.list.push(content)
        }
      })
    },
    detail (weather, vitual, ticketReceiver, code, ticketCode) {
      if (this.click_once[0].one) {
        return
      }
      this.click_once[0].one = true
      setTimeout(() => {
        this.click_once[0].one = false
      }, 300)
      if (vitual) {
        this.$parent.Tips('已领取，请到我的会员查看')
        return
      }

      if (!ticketReceiver) {
        this.$parent.userData('zz', 2, 1, ticketCode) // code 1 是暂未付而已
        return
      }

      const data = JSON.parse(ticketReceiver)
      console.log(data)
      const staticData = `<p>收件人:<span class="data-span">${data.user_name}</span></p><p>手机号:<span class="data-span">${data.user_number}</span></p><p>地址:<span class="data-span">${data.user_address}</span></p>`
      this.$parent.userData(staticData, 1, code)
    }
  },
  watch: {
    list () {
      this.list.length === 0 ? this.list_null = true : this.list_null = false
      // this.$nextTick(this.$parent.bScroll)
    }
  },
  created () {
  },
  mounted () {
    setTimeout(this.$parent.bScroll, 40)
    this.getRewardData()
  }
}
</script>

